.App {
  text-align: center;
  padding: 20px;
}

form {
  margin-bottom: 20px;
}

input {
  padding: 8px;
  margin-right: 10px;
}

button {
  padding: 8px;
  margin-left: 5px;
  cursor: pointer;
}

/* Add spacing and cleaner layout */
.task {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 10px;
  margin-bottom: 10px;
  background-color: #f5f5f5;
  border-radius: 5px;
}

.task-buttons {
  display: flex;
  gap: 10px;
}

.task-buttons button {
  padding: 5px 10px;
  border-radius: 5px;
  border: 1px solid #ccc;
}

/* Priority-based background colors */
.low-priority {
  background-color: lightgreen;
}

.medium-priority {
  background-color: yellow;
}

.high-priority {
  background-color: red;
}  

.overdue {
  background-color: #ffcccc; /* Light red for overdue tasks */
}

.overdue {
  background-color: lightcoral;
  text-decoration: line-through;
} 

.task {
  display: flex;
  justify-content: space-between;
  margin-bottom: 10px;
}

.task.overdue {
  background-color: #ffdddd; /* Highlight overdue tasks in red */
}

.task-buttons button {
  margin-left: 5px;
}

.dark-mode {
  background-color: #333;
  color: white;
}

.light-mode {
  background-color: white;
  color: black;
}

.dark-mode {
  background-color: #121212;
  color: white;
}

.light-mode {
  background-color: white;
  color: black;
}

.fade-out {
  animation: fadeOut 0.5s forwards;
}

@keyframes fadeOut {
  from { opacity: 1; }
  to { opacity: 0; }
}
